#container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

header {
  position: fixed;
  display: block;
  width: 100%;
  z-index: 1000;
  transition: all 0.3s ease-in-out;
}

main {
  flex: 1 1 auto;
  position: relative; /* need this to position inner content */
}
